<template>
  <div class="t-inline-block magic-ui-table-module">
    <el-table
      :data="tableDataList"
      style="width: 100%"
      size="small"
      ref="tableRef"
      :style="style"
      :header-cell-style="headerCellStyle"
      :border="props.config.border"
      :stripe="props.config.stripe"
      :max-height="props.config.maxHeight"
    >
      <el-table-column
        type="index"
        width="50"
        label="序号"
        :fixed="config.index && config.fixedCol === 'first' ? 'left' : false"
        v-if="config.index"
      />
      <el-table-column
        v-for="(item, index) of config.dataSourceModel.tagList"
        :label="item.label"
        :key="item.label"
        class-name="table-cell"
        min-width="60"
        :width="getWidth(item)"
        :fixed="getFixed(index)"
      >
        <template #default="scope">
          <!-- 文本 -->
          <span class="col-cell text" :style="{ color: item.color }" v-if="item.type === 'text' || !item.type">{{
            scope.row[item.prop]
          }}</span>
          <!-- tag标签 -->
          <span class="col-cell" v-if="item.type === 'tag'">
            <div class="tag-list-cell">
              <span
                class="tag-cell"
                v-for="(cell, tagIndex) in scope.row[item.prop]?.toString().split(',')"
                :key="cell"
                :style="{ backgroundColor: getTagBg(tagIndex) }"
                >{{ cell }}</span
              >
            </div>
          </span>
          <!-- tag标签渐变色 -->
          <span class="col-cell" v-if="item.type === 'tag_gradient'">
            <div class="tag-list-cell">
              <span
                class="tag-cell"
                v-for="(cell, tagIndex) in scope.row[item.prop]?.toString().split(',')"
                :key="cell"
                :style="{ backgroundImage: getTagGradientBg(tagIndex) }"
                >{{ cell }}</span
              >
            </div>
          </span>
          <!-- 进度条 -->
          <span
            class="col-cell"
            v-if="item.type === 'progress'"
            :style="{ height: item.height ? item.height + 'px' : '' }"
          >
            <span
              class="progress-cell"
              :class="{ radius: item.showRadius }"
              :style="{
                backgroundColor: getProgressBg(scope.row[item.prop]),
                width: `${scope.row[item.prop]}%`,
              }"
              >&nbsp;</span
            >
            <span class="value">{{ scope.row[item.prop] }}%</span>
          </span>
          <!-- 进度条 -->
          <span
            class="col-cell"
            v-if="item.type === 'progress_gradient'"
            :style="{ height: item.height ? item.height + 'px' : '' }"
          >
            <span
              class="progress-cell"
              :class="{ radius: item.showRadius }"
              :style="{
                backgroundImage: getProgressGradientBg(scope.row[item.prop]),
                width: `${scope.row[item.prop]}%`,
              }"
              >&nbsp;</span
            >
            <span class="value">{{ scope.row[item.prop] }}%</span>
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script lang="ts" setup>
import { computed, ref } from 'vue';
import { ElTable, ElTableColumn } from 'element-plus';

import 'element-plus/lib/components/table/style/css';

import useApp from '../../useApp';
const props = defineProps<{
  config: any;
  // model: Object;
}>();
useApp(props);
const tableRef = ref();

const bgColor = ['#EBADBA', '#7E57F2', '#5ACEDB', '#6EF257', '#E8CC54'];

// 进度条渐变色数据
type val = number;
const gradientBGMap = [
  [(val: val) => val <= 20, () => 'linear-gradient(220.55deg, #DDE4FF 0%, #8DA2EE 100%)'],
  [(val: val) => val > 20 && val <= 40, () => 'linear-gradient(220.55deg, #FF5EEF 0%, #456EFF 100%)'],
  [(val: val) => val > 40 && val <= 60, () => 'linear-gradient(220.55deg, #00E0EE 0%, #AD00FE 100%)'],
  [(val: val) => val > 60 && val <= 80, () => 'linear-gradient(220.55deg, #FFE70B 0%, #27B643 100%)'],
  [(val: val) => val >= 100, () => 'linear-gradient(220.55deg, #FFC328 0%, #E20000 100%)'],
];
// tag渐变色数据
const gradientMap = [
  [(val: val) => val <= 20, () => '#EBADBA'],
  [(val: val) => val > 20 && val <= 40, () => '#7E57F2'],
  [(val: val) => val > 40 && val <= 60, () => '#5ACEDB'],
  [(val: val) => val > 60 && val <= 80, () => '#6EF257'],
  [(val: val) => val >= 100, () => '#E8CC54'],
];

const gradientBG = [
  'linear-gradient(220.55deg, #DDE4FF 0%, #8DA2EE 100%)',
  'linear-gradient(220.55deg, #FF5EEF 0%, #456EFF 100%)',
  'linear-gradient(220.55deg, #00E0EE 0%, #AD00FE 100%);',
  'linear-gradient(220.55deg, #FFE70B 0%, #27B643 100%)',
  'linear-gradient(220.55deg, #FFC328 0%, #E20000 100%)',
];
// 获取tag背景色
const getTagBg = (val: number) => {
  val = val % 5; // 取余数
  return bgColor[val];
};
const getTagGradientBg = (val: number) => {
  val = val % 5; // 取余数
  return gradientBG[val];
};
const headerCellStyle = () => {
  const { headerShowBorder = true, headerBgColor, headerFontColor } = props.config;
  const styleObj = {
    borderRight: headerShowBorder ? undefined : 'none',
    backgroundColor: headerBgColor,
    color: headerFontColor,
  };
  return styleObj;
};
// 根据数值获取进度条背景色
const getProgressBg = (val: number) => {
  let color;
  const getColor = gradientMap.find((item) => item[0](val));
  if (getColor) {
    color = getColor[1](val);
  } else {
    color = '#EBADBA';
  }
  return typeof color === 'string' ? color : '#EBADBA';
};
const getProgressGradientBg = (val: number) => {
  let color;
  const getColor = gradientBGMap.find((item) => item[0](val));

  if (getColor) {
    color = getColor[1](val);
  }

  return typeof color === 'string' ? color : 'linear-gradient(220.55deg, #DDE4FF 0%, #8DA2EE 100%)';
};

// eslint-disable-next-line vue/no-setup-props-destructure
const { borderColor: originalBorderColor, stripeColor: originalStripeColor } = props.config;
const style = computed(() => {
  const {
    border,
    borderColor,
    stripe,
    stripeColor,
    borderRadiusLeftTop,
    borderRadiusRightTop,
    borderRadiusLeftBottom,
    borderRadiusRightBottom,
  } = props.config;

  return {
    '--el-border-color-lighter': border ? borderColor : originalBorderColor,
    '--el-fill-color-lighter': stripe ? stripeColor : originalStripeColor,
    'border-top-left-radius': `${borderRadiusLeftTop}px`,
    'border-top-right-radius': `${borderRadiusRightTop}px`,
    'border-bottom-left-radius': `${borderRadiusLeftBottom}px`,
    'border-bottom-right-radius': `${borderRadiusRightBottom}px`,
  };
});

const getFixed = (i: number) => {
  const { index, fixedCol, dataSourceModel } = props.config;

  let fixedValue: 'left' | 'right' | false = false;
  if (!index && fixedCol) {
    if (fixedCol === 'first') {
      if (i === 0) {
        fixedValue = 'left';
      }
    } else {
      if (i === dataSourceModel.tagList.length - 1) {
        fixedValue = 'right';
      }
    }
  }
  return fixedValue;
};
const getWidth = (item: any) => {
  const { width } = item;
  return width > 0 ? Number(width) : undefined;
};
const tableDataList = computed(() => {
  const list = props.config?.dataSourceModel?.list;
  list.map((item: any) => {
    const span = document.createElement('span');
    span.innerHTML = item.label;
    const spanElementList: any = span.querySelectorAll('.magic-ui__dynamic-data-link');
    for (let i = 0; i < spanElementList?.length; i++) {
      const dataValue = spanElementList[i].getAttribute('data-value');
      spanElementList[i].innerText = dataValue;
    }
    item.label = span.innerText;
  });
  return list;
});
</script>

<style lang="scss" scoped>
.t-inline-block {
  display: inline-block;
}
.col-cell {
  display: flex;
  position: relative;
  height: 100%;
  .value {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .tag-list-cell {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    .tag-cell {
      color: #fff;
      display: flex;
      padding: 2px 10px;
      align-items: center;
      justify-content: center;
      border-radius: 2px;
    }
  }

  .progress-cell {
    display: flex;
    align-items: center;
    color: #fff;
    justify-content: flex-end;
    padding: 2px 0 2px 0;
    height: 100%;
    &.radius {
      border-radius: 99px;
    }
  }
}
</style>
